<template>
  <div class="hello">
    <van-nav-bar
      title="标题"
      left-text="返回"
      right-text="按钮"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <img src="../assets/logo.png">
    pda测试页面
    <van-dialog v-model="show" title="标题" show-cancel-button>
      <p>引入vant-UI组件库</p>
    </van-dialog>
    <van-grid>
      <van-grid-item icon="photo-o" text="文字1" />
      <van-grid-item icon="photo-o" text="文字2" />
      <van-grid-item icon="photo-o" text="文字3" />
      <van-grid-item icon="photo-o" text="文字4" />
    </van-grid>
    <van-button type="primary" @click="handleShow">我是一个小按钮啊</van-button>
    <button @click="getposition(1)">获取位置</button>
    <span>{{msg}}</span><br/>
    <button @click="getimage()">显示本地图片</button>
    <button @click="getcode()">获取二维码</button><br/>
    <span>{{codeinfo}}</span>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      show: false,
      msg:'cordova not init',
      codeinfo:''
    }
  },
  methods: {
    handleShow () {
      this.show = true
    },
    onClickLeft(){
      this.$router.go(-1)
    },
    onClickRight(){
      console.log('button')
    },
    getcode()
    {
      var me = this;
      cordova.plugins.barcodeScanner.scan(
      function (result) {
          me.codeinfo="We got a barcode\n" +
                "Result: " + result.text + "\n" +
                "Format: " + result.format + "\n" +
                "Cancelled: " + result.cancelled;
      }, 
      function (error) {
          alert("Scanning failed: " + error);
      }
   );
    },
    getimage()
    {
      var me= this;
      navigator.camera.getPicture(onSuccess, onFail, { quality: 50,
          destinationType: Camera.DestinationType.FILE_URI,sourceType:0 });

      function onSuccess(imageURI) {          
          me.imgsrc = imageURI;
      }

      function onFail(message) {
          alert('Failed because: ' + message);
      }
    },
    getposition:function(val)
    {
      navigator.vibrate(500);
      var me = this;
       var onSuccess = function(position) {
        me.msg='Latitude:\t'          + position.coords.latitude          + '\n' +
              'Longitude:\t'         + position.coords.longitude         + '\n' +
              'Altitude:\t'          + position.coords.altitude          + '\n' +
              'Accuracy:\t'          + position.coords.accuracy          + '\n' +
              'Altitude Accuracy:\t' + position.coords.altitudeAccuracy  + '\n' +
              'Heading:\t'           + position.coords.heading           + '\n' +
              'Speed:\t'             + position.coords.speed             + '\n' +
              'Timestamp:\t'         + position.timestamp                + '\n';
    };

    // onError Callback receives a PositionError object
    //
    var error= function(error) {
        me.msg='code: '    + error.code    + '\n' +
              'message: ' + error.message + '\n';
    }

    navigator.geolocation.getCurrentPosition(onSuccess, error,{maximumAge: 30000, timeout: 30000, enableHighAccuracy: val});
    }
  },
  mounted(){
    document.addEventListener("deviceready", onDeviceReady, false);
    var me = this;
    console.log(cordova,'home')
    function onDeviceReady() {
        me.msg="cordova is ready";
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.hello{
  text-align: center;
  font-size: 16px;
  // background:green;
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  img{
    width: 200px;
    height: 200px;
  }
}
</style>



